import axios from 'axios';
import { Formik } from 'formik';
import React from 'react';
import { useMutation } from 'react-query';
import { useDispatch } from 'react-redux';

import { serverPath } from '../../constants/noteList';
import { getUserInfo } from '../../redux/actions/actionCreators';
import { initValuesForSignUp, ValidSchemeForSignUp } from './validation';
import {
    SignForm,
    SignInButton,
    RegularText,
    StyledLink,
    StyledTitle,
} from './styled';
import ValidInput from './validInput';

const SignUp = () => {
    const dispatch = useDispatch();
    const mutation = useMutation((dataToPost) => {
        dispatch(getUserInfo(dataToPost));
        return axios.post(serverPath, dataToPost);
    });

    return (
        <Formik
            initialValues={initValuesForSignUp}
            validationSchema={ValidSchemeForSignUp}
            onSubmit={async (values) => {
                const dataToSend = {
                    firstName: values.firstName,
                    lastName: values.lastName,
                    dateOfBirth: values.dateOfBirth,
                    password: values.password,
                    email: values.email,
                };
                mutation.mutate(dataToSend);
            }}
        >
            {({ errors }) => (
                <SignForm>
                    <StyledTitle>sign up</StyledTitle>
                    <ValidInput name='firstName' label="Enter first name" error={errors.firstName} type='text' autoComplete='true' />
                    <ValidInput name='lastName' label="Enter last name" error={errors.lastName} type='text' autoComplete='true' />
                    <ValidInput name='dateOfBirth'  error={errors.dateOfBirth} type='date' autoComplete='false' />
                    <ValidInput name='email' label="Enter email" error={errors.email} type='email' autoComplete='true' />
                    <ValidInput name='password' label="Enter password" error={errors.password} type='password' autoComplete='false' />
                    <ValidInput name='confirmPassword' label="Confirm password" error={errors.confirmPassword} type='password' autoComplete='false' />
                    <SignInButton type="submit">Sign up</SignInButton>
                    <RegularText>or</RegularText>
                    <StyledLink to="/">Sign in</StyledLink>
                </SignForm>
            )}
        </Formik>
    );
};

export default SignUp;
